#mainContainer {
    grid-column: 3;
    grid-row: 3;
    border: 1px solid black;
    min-height: 100%;
}
#rancherImage {
    grid-row: 1;
    grid-column: 2;
}
#rancherConversation {
    grid-row: 1;
    grid-column: 1;
}
#partnerPokemonContainer {
    grid-row: 2;
    grid-column: 1 / 3;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
}